<template>
  <div class="palylist">
    <HomeLink></HomeLink>
    <div class="head">
      <div class="left">
        <div class="listImg">
          <img :src="songListDetail.coverImgUrl" />
        </div>
      </div>
      <div class="right">
        <div class="rUp">
          <h3>云音乐飙升榜</h3>
          <p>
            <span class="iconfont">&#xe64f;</span>
            <span v-if="this.dayNumber>0">最近更新：{{dayNumber}}天前(每周四更新)</span>
            <span v-else>最近更新：今天(每周四更新)</span>
          </p>
        </div>
        <div class="rDown">
          <button>
            <span class="iconfont">&#xe524;</span>
            <span>播放</span>
          </button>
          <button>
            <span class="iconfont">&#xe610;</span>
            <span>({{songListDetail.subscribedCount}})</span>
          </button>
          <button>
            <span class="iconfont">&#xe605;</span>
            <span>({{songListDetail.shareCount}})</span>
          </button>
          <button>
            <span class="iconfont">&#xe632;</span>
            <span>下载</span>
          </button>
          <button>
            <span class="iconfont">&#xf0302;</span>
            <span>({{songListDetail.commentCount}})</span>
          </button>
        </div>
      </div>
    </div>
    <div class="songLnfo">
      <div class="title">
        <span>歌曲列表</span>
        <span>{{songListLength}}首歌</span>
      </div>
      <ul>
        <li v-for="(track,index) in songListDetail.tracks" :key="index">
          <span class="number">{{index+1}}</span>
          <img :src="track.al.picUrl" />
          <span class="songName">{{track.al.name}}</span>
          <i class="icon icon-play"></i>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import HomeLink from "@/components/HomeLink.vue";
export default {
  components: {
    HomeLink
  },
  data() {
    return {
      songListDetail: Object,
      dayNumber: Number,
      songListLength: Number
    };
  },
  beforeRouteEnter(to, from, next) {
    window.axios
      .get("/top/list", {
        params: {
          idx: 1
        }
      })
      .then(response => {
        next(vm => {
          vm.songListDetail = response.data.playlist;
          vm.getUpDatetime();
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    getUpDatetime: function() {
      let timeNumber = Number(
        String(Date.now() - this.songListDetail.updateTime).substring(0, 6)
      );
      // console.log(timeNumber);
      this.dayNumber = Math.floor(timeNumber / 86400);
      this.songListLength = this.songListDetail.tracks.length;
      // console.log(this.dayNumber);
    }
  }
};
</script>
<style lang="less" scoped>
@font-face {
  font-family: "iconfont";
  src: url("../assets/hotFonts/iconfont.eot");
  src: url("../assets/hotFonts/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../assets/hotFonts/iconfont.woff2") format("woff2"),
    url("../assets/hotFonts/iconfont.woff") format("woff"),
    url("../assets/hotFonts/iconfont.ttf") format("truetype"),
    url("../assets/hotFonts/iconfont.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.palylist {
  .head {
    width: 100%;
    height: 120px;
    display: flex;
    padding: 3px;
    margin-top: 20px;
    .left {
      flex: 3;
      height: 120px;
      padding: 5px;
      border: 1px solid #cccccc;
    }
    .right {
      margin-left: 5px;
      flex: 7;
      height: 120px;
      .rUp {
        padding: 5px;
        h3 {
          font-size: 18px;
        }
        p {
          span {
            font-size: 12px;
          }
          span:nth-child(1) {
            font-weight: 700;
          }
          span:nth-child(2) {
            margin-left: 5px;
          }
        }
      }
      .rDown {
        button {
          padding: 3px;
          margin-right: 3px;
          margin-top: 3px;
          // border-radius: 3px;
        }
      }
    }
  }
  .songLnfo {
    margin-top: 20px;
    .title {
      width: 100%;
      height: 30px;
      border-bottom: 2px solid #c20c0c;
      span {
        font-size: 16px;
        color: black;
        margin-left: 10px;
      }
      span:nth-child(2) {
        font-size: 12px;
        // margin-left: 10px;
        color: gray;
      }
    }
    ul {
      padding: 5px;
      li {
        padding: 5px;
        display: flex;
        position: relative;
        // justify-content: center;
        align-items: center;
        .number {
          display: inline-block;
          padding: 5px;
          font-size: 16px;
          color: gray;
        }
        img {
          height: 35px;
          width: 35px;
          margin-left: 15px;
        }
        .songName {
          margin-left: 10px;
          font-size: 12px;
          color: gray;
        }
        .icon {
          background-image: url("../assets/index_icon_2x.png");
          display: inline-block;
          background-size: 166px auto;
          // vertical-align: middle;
          &.icon-play {
            width: 25px;
            height: 25px;
            background-position-x: -23px;
            position: absolute;
            top: 10px;
            right: 10px;
            // animation: changeColor 0.5s linear;
          }
        }
      }
      li:nth-of-type(odd) {
        background: #f7f7f7;
      }
    }
  }
}
</style>