<template>
  <div class="palylist">
    <HomeLink></HomeLink>
    <div class="searchInPut">
      <div class="inputBox">
        <span class="iconfont" ref="iconfont">&#xe687;</span>
        <input
          type="text"
          :placeholder="searchDefault"
          v-model="inputWord"
          @click="searchshow=true"
        />
      </div>
      <div class="cancel" @click="searchshow=false">取消</div>
      <div class="searchAnswer" ref="searchAnswer" v-if="searchshow">
        <ul>
          <li v-for="(answer,index) in searchAnswer.songs" :key="index">
            <div class="anLeft">
              <img :src="answer.artists[0].img1v1Url" />
            </div>
            <div class="abright">
              <div class="songName">{{answer.name}}</div>
              <div class="art">{{answer.artists[0].name}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="searchHistory">
      <span>搜索历史</span>
      <span class="iconfont">&#xe614;</span>
      <div class="history"></div>
    </div>
    <div class="searchHotWords">
      <div class="title">热搜榜</div>
      <ul>
        <li v-for="(searchList,index) in searchList.data" :key="index">
          <div class="left">{{index+1}}</div>
          <div class="right">
            <div class="up">
              <span>{{searchList.searchWord}}</span>
              <img :src="searchList.iconUrl" />
            </div>
            <div class="down">
              <span>{{searchList.content}}</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import HomeLink from "@/components/HomeLink.vue";
export default {
  components: {
    HomeLink
  },
  data() {
    return {
      searchDefault: String,
      searchList: Object,
      searchHistroy: Object,
      inputWord: "",
      searchAnswer: Object,
      searchshow: false
    };
  },
  watch: {
    inputWord: function() {
      // console.log(this.inputWord);
      window.axios
        .get("/search", {
          params: {
            keywords: this.inputWord
          }
        })
        .then(response => {
          this.searchAnswer = response.data.result;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  methods: {
    getdefaultWord: function() {
      window.axios
        .get("/search/default")
        .then(response => {
          this.searchDefault = response.data.data.showKeyword;
          // console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    window.axios
      .get("/search/hot/detail")
      .then(response => {
        next(vm => {
          vm.searchList = response.data;
          vm.getdefaultWord();
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>
<style lang="less" scoped>
@font-face {
  font-family: "iconfont";
  src: url("../assets/searchFonts/iconfont.eot");
  src: url("../assets/searchFonts/iconfont.eot?#iefix")
      format("embedded-opentype"),
    url("../assets/searchFonts/iconfont.woff2") format("woff2"),
    url("../assets/searchFonts/iconfont.woff") format("woff"),
    url("../assets/searchFonts/iconfont.ttf") format("truetype"),
    url("../assets/searchFonts/iconfont.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  height: 100%;
  .searchInPut {
    width: 100%;
    height: 50px;
    padding: 10px;
    position: relative;
    .inputBox {
      width: 80%;
      height: 30px;
      border: 2px solid lightgray;
      border-radius: 30px;
      display: flex;
      align-items: center;
      span {
        margin-left: 5px;
      }
      input {
        // float: right;
        width: 240px;
        margin-left: 5px;
        border: none;
        outline: none;
      }
    }
    .cancel {
      // float: right;
      position: absolute;
      top: 10px;
      right: 15px;
      // border: 1px solid gray;
    }
  }
  .searchHotWords {
    width: 100%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    .title {
      width: 100%;
      padding: 5px;
      font-size: 18px;
    }
    .title::before {
      content: "";
      height: 34px;
      border-left: 4px solid #d43c33;
      margin-right: 5px;
    }
    ul {
      width: 100%;
      li {
        width: 100%;
        display: flex;
        margin-top: 10px;
        // flex-wrap: nowrap;
        .left {
          flex: 1;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .right {
          flex: 9;
          span {
            font-size: 16px;
          }
          .up {
            img {
              height: 15px;
              margin-left: 5px;
            }
          }
          .down {
            span {
              font-size: 12px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              color: lightgray;
            }
          }
        }
      }
    }
  }
  .searchAnswer {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    z-index: 99;
    background: white;
    height: 300vh;
    ul {
      li {
        width: 100%;
        padding: 10px;
        display: flex;
        .anLeft{
          flex: 1;
          img{
            width: 40px;
            height: 40px;
            // padding: 10px;
          }
        }
        .abright {
          flex: 9;
          margin-left: 10px;
          .art {
            font-size: 12px;
            color: lightgray;
          }
        }
      }
    }
  }
}
</style>